.blinking {
    animation: blink 1s ease-in-out infinite;
}

@keyframes blink {
    0% {
        background: rgb(246, 137, 8, 0);
        background: radial-gradient(circle, rgba(246, 137, 8, 0) 0%, rgba(255, 255, 255, 0) 80%);
    }

    100% {
        background: rgb(246, 137, 8, 0.5);
        background: radial-gradient(circle, rgba(246, 137, 8, 0.5) 0%, rgba(255, 255, 255, 1) 80%);
    }
}
