input[type='range'].custom-range {
    -webkit-appearance: none;
    &:focus {
        outline: none;
    }
    &::-webkit-slider-runnable-track {
        height: 30px;
        cursor: pointer;
        background: white;
        border-radius: 30px;
        border: 1px solid #474747;
    }
    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        border: 0px solid #000000;
        height: 30px;
        width: 39px;
        border-radius: 7px;
        background: rgb(59, 130, 246);
    }
    &::-moz-range-thumb {
        border: 0px solid #000000;
        height: 30px;
        width: 39px;
        border-radius: 7px;
        background: rgb(59, 130, 246);
    }
}