html {
    overflow: hidden;
    margin: 0;
}

body {
  margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

tbody {
  vertical-align: top !important;
}

.sns-font-size-lg {
  font-size: 1.5em;
}

#root {
    background-image: repeating-linear-gradient(185deg, rgba(88,88,88, 0.03) 0px, rgba(88,88,88, 0.03) 12px,transparent 12px, transparent 13px,rgba(88,88,88, 0.03) 13px, rgba(88,88,88, 0.03) 18px,transparent 18px, transparent 26px),repeating-linear-gradient(120deg, rgba(88,88,88, 0.03) 0px, rgba(88,88,88, 0.03) 6px,transparent 6px, transparent 14px,rgba(88,88,88, 0.03) 14px, rgba(88,88,88, 0.03) 26px,transparent 26px, transparent 38px),repeating-linear-gradient(362deg, rgba(88,88,88, 0.03) 0px, rgba(88,88,88, 0.03) 12px,transparent 12px, transparent 15px,rgba(88,88,88, 0.03) 15px, rgba(88,88,88, 0.03) 20px,transparent 20px, transparent 30px),repeating-linear-gradient(185deg, rgba(88,88,88, 0.03) 0px, rgba(88,88,88, 0.03) 5px,transparent 5px, transparent 7px,rgba(88,88,88, 0.03) 7px, rgba(88,88,88, 0.03) 12px,transparent 12px, transparent 23px),repeating-linear-gradient(367deg, rgba(88,88,88, 0.03) 0px, rgba(88,88,88, 0.03) 6px,transparent 6px, transparent 17px,rgba(88,88,88, 0.03) 17px, rgba(88,88,88, 0.03) 28px,transparent 28px, transparent 29px),repeating-linear-gradient(179deg, rgba(88,88,88, 0.03) 0px, rgba(88,88,88, 0.03) 6px,transparent 6px, transparent 7px,rgba(88,88,88, 0.03) 7px, rgba(88,88,88, 0.03) 12px,transparent 12px, transparent 21px),linear-gradient(127deg, rgb(246,246,246),rgb(246,246,246));
}

button {
    // Remove the outline of all buttons when focused
    // Display is going to be mostly used on Brightsign displays
    // where there is no keyboard that makes the outline useful
    @apply focus:outline-none;
}

.shake-animation {
    animation: shake3856 0.3s linear infinite both;
}

@keyframes shake3856 {
 0% {
  -webkit-transform: translate(0);
  transform: translate(0);
 }

 20% {
  -webkit-transform: translate(-2px, 2px);
  transform: translate(-2px, 2px);
 }

 40% {
  -webkit-transform: translate(-2px, -2px);
  transform: translate(-2px, -2px);
 }

 60% {
  -webkit-transform: translate(2px, 2px);
  transform: translate(2px, 2px);
 }

 80% {
  -webkit-transform: translate(2px, -2px);
  transform: translate(2px, -2px);
 }

 100% {
  -webkit-transform: translate(0);
  transform: translate(0);
 }
}